import React from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  BarElement,
  TimeScale,
} from "chart.js";
import zoomPlugin from "chartjs-plugin-zoom";
import 'chartjs-adapter-date-fns';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  TimeScale,
  zoomPlugin
);

function StockChart({ data, selectedIndicators, displayPeriod }) {
  const { historical, predicted, indicators } = data;

  // Compute startDate based on displayPeriod
  let startDate = new Date();
  const lastHistoricalDate = historical.dates[historical.dates.length - 1];
  const firstHistoricalDate = historical.dates[0];
  const lastPredictedDate = predicted.dates[predicted.dates.length - 1] || lastHistoricalDate;

  const lastDateObj = new Date(lastHistoricalDate);
  startDate = new Date(lastDateObj);

  if (displayPeriod !== 'max') {
    switch (displayPeriod) {
      case '1d': startDate.setDate(lastDateObj.getDate() - 1); break;
      case '5d': startDate.setDate(lastDateObj.getDate() - 5); break;
      case '1m': startDate.setMonth(lastDateObj.getMonth() - 1); break;
      case '3m': startDate.setMonth(lastDateObj.getMonth() - 3); break;
      case '6m': startDate.setMonth(lastDateObj.getMonth() - 6); break;
      case 'ytd': startDate = new Date(lastDateObj.getFullYear(), 0, 1); break;
      case '1y': startDate.setFullYear(lastDateObj.getFullYear() - 1); break;
      case '5y': startDate.setFullYear(lastDateObj.getFullYear() - 5); break;
      default: break;
    }
  } else {
    startDate = new Date(firstHistoricalDate);
  }

  const startDateStr = startDate.toISOString().slice(0, 10);
  const minLimit = firstHistoricalDate;
  const maxLimit = lastPredictedDate;

  // Prepare datasets with {x, y}
  let datasets = [
    {
      label: "Historical Prices",
      data: historical.dates.map((d, i) => ({ x: d, y: historical.prices[i] })),
      borderColor: "blue",
      backgroundColor: "rgba(0, 0, 255, 0.1)",
      fill: false,
      pointRadius: 0,
      yAxisID: 'y',
    },
    {
      label: "Predicted Prices",
      data: predicted.dates.map((d, i) => ({ x: d, y: predicted.prices[i] })),
      borderColor: "green",
      backgroundColor: "rgba(0, 255, 0, 0.1)",
      fill: false,
      borderDash: [5, 5],
      pointRadius: 0,
      yAxisID: 'y',
    },
  ];

  // Add indicators based on selected
  if (indicators) {
    if (selectedIndicators.includes('rsi') && indicators.rsi) {
      datasets.push({
        label: "RSI (14)",
        data: historical.dates.map((d, i) => ({ x: d, y: indicators.rsi[i] })),
        borderColor: "purple",
        yAxisID: 'y1',
        pointRadius: 0,
      });
    }
    if (selectedIndicators.includes('macd') && indicators.macd) {
      datasets.push({
        label: "MACD Line",
        data: historical.dates.map((d, i) => ({ x: d, y: indicators.macd.macd[i] })),
        borderColor: "red",
        yAxisID: 'y2',
        pointRadius: 0,
      });
      datasets.push({
        label: "MACD Signal",
        data: historical.dates.map((d, i) => ({ x: d, y: indicators.macd.signal[i] })),
        borderColor: "orange",
        yAxisID: 'y2',
        pointRadius: 0,
      });
      datasets.push({
        type: 'bar',
        label: "MACD Histogram",
        data: historical.dates.map((d, i) => ({ x: d, y: indicators.macd.histogram[i] })),
        backgroundColor: "rgba(0, 255, 0, 0.5)",
        yAxisID: 'y2',
      });
    }
    if (selectedIndicators.includes('sma50') && indicators.sma50) {
      datasets.push({
        label: "SMA (50)",
        data: historical.dates.map((d, i) => ({ x: d, y: indicators.sma50[i] })),
        borderColor: "cyan",
        yAxisID: 'y',
        pointRadius: 0,
      });
    }
    if (selectedIndicators.includes('ema200') && indicators.ema200) {
      datasets.push({
        label: "EMA (200)",
        data: historical.dates.map((d, i) => ({ x: d, y: indicators.ema200[i] })),
        borderColor: "magenta",
        yAxisID: 'y',
        pointRadius: 0,
      });
    }
  }

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    interaction: {
      mode: 'index',
      intersect: false,
    },
    plugins: {
      title: { display: true, text: "Stock Price Chart" },
      legend: { position: "top" },
      tooltip: {
        enabled: true,
      },
      zoom: {
        pan: {
          enabled: true,
          mode: 'x',
        },
        zoom: {
          wheel: {
            enabled: true,
          },
          pinch: {
            enabled: true,
          },
          mode: 'x',
        },
        limits: {
          x: {
            min: minLimit,
            max: maxLimit,
          },
        },
      },
    },
    scales: {
      x: {
        type: 'time',
        time: {
          unit: 'day',
          parser: 'yyyy-MM-dd',
        },
        title: { display: true, text: "Date" },
        ticks: {
          autoSkip: true,
          maxTicksLimit: 20,
          maxRotation: 45,
          minRotation: 45,
        },
        min: startDateStr,
        max: maxLimit,
      },
      y: {
        type: 'linear',
        display: true,
        position: 'left',
        title: { display: true, text: "Price (USD)" },
      },
      y1: {
        type: 'linear',
        display: selectedIndicators.includes('rsi'),
        position: 'right',
        grid: { drawOnChartArea: false },
        title: { display: true, text: "RSI" },
        min: 0,
        max: 100,
      },
      y2: {
        type: 'linear',
        display: selectedIndicators.includes('macd'),
        position: 'right',
        grid: { drawOnChartArea: false },
        title: { display: true, text: "MACD" },
      },
    },
  };

  return <Line options={options} data={{ datasets }} />;
}

export default StockChart;